<template>
   <swiper v-if="swiperData.length > 0" :options="swiperOptionBanner" class="swiper-banner">
      <swiper-slide v-for="(slide,index) in swiperData" :key="index">
        <div class="img-cover" :style="{backgroundImage: 'url('+slide.url+')'}"></div>
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
</template>

<script>
export default {
  props: ['swiperData'],
  data(){
    return {
       swiperOptionBanner: {
        autoplay: {
          disableOnInteraction: false,
        },
        loop: true,
        speed: 315,
        notNextTick: true,
        observer:true,
        observeParents:true,
        preloadImages:false,
        lazyLoading : true,
        resistanceRatio: 0,
        pagination: {
          el: ".swiper-pagination"
        }
      },
      // swiperData: [],
    }
  },
  mounted(){
    // //模拟banner数据
    // var bannerData = [
     
    // ]
    // this.swiperData = bannerData
  }
}
</script>

<style lang="scss" scoped>

.swiper-container{
  overflow: hidden;
}
.swiper-banner{
  margin-bottom: px2rem(16);
  height: px2rem(250);
  .swiper-slide{
    height: px2rem(250);
  }
  .img-cover{
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
  }
}
</style>



