<template>
  <ul class="ej-list-3">
    <li class="ej-item" v-for="(item,index) in activityList" :key="index">
      <div class="ej-item-inner">
        <div class="img-inner">
          <img :src="item.img" alt="">
        </div>
        <div class="con-inner">
          <h3 class="title ellipsis-2" v-text="item.title"></h3>
          <p class="location ellipsis" v-text="item.location"></p>
          <p class="desc clearfix">
            <span class="time" v-text="item.time"></span>
            <span class="price fr" v-text="item.price"></span>
          </p>
        </div>
      </div>
      <div class="ej-item-bottom text-right">
        <button :class="item.status === 0?'active':'disabled'" v-text="status[item.status]"></button>
      </div>
    </li>
  </ul>
</template>

<script>
export default {
  props: ['activityList'],
  data(){
    return{
      status:['立即报名','进行中','已结束']
    }
  },
  methods:{

  }
}
</script>

<style lang="scss" scoped>

.ej-list-3{
  .ej-item{
    padding: px2rem(20) px2rem(32);
    margin-bottom: px2rem(16);
    background-color: #fff;
  }
  .ej-item-bottom{
    margin-top: px2rem(36);
    button{
      display: inline-block;
      vertical-align: middle;
      width: px2rem(150);
      height: px2rem(52);
      font-size: px2rem(28);
      color: #fff;
      text-align: center;
      line-height: normal;
      padding: 0;
      &.active{
        background-color: #f26d21;
      }
      &.disabled{
        background-color: #6c6f71;
      }
    }
  }
  .ej-item-inner{
    display: flex;
    .img-inner{
      img{
        width: px2rem(300);
        height: px2rem(180);
      }
    }
    .con-inner{
      min-width: 0;
      padding-left: px2rem(20);
      flex: 1;
      align-self: center;
      .title{
        font-size:  px2rem(32);
        height:  px2rem(80);
        line-height:  px2rem(40);
      }
      .location{
        margin-top:  px2rem(20);
        font-size:  px2rem(28);
        color: #6c6f71;
      }
      .desc{
        margin-top:  px2rem(20);
        font-size:  px2rem(28);
        .time{
          color: #6c6f71;
        }
        .price{
          color: #f26d21;
        }
      }
    }
  }
}
</style>



