<template>
  <div class="header-inner">
    <div class="logo">
        <img src="http://temp.im/150x60/30407d/fff" alt="">
    </div>
    <span class="tool-bar"><i class="fa fa-search"></i><i class="fa fa-qrcode"></i></span>
</div>
</template>

<script>
export default {

}
</script>

<style lang="scss" scoped>
.header-inner{
  position: relative;
  height: px2rem(100);
  display: flex;
  align-items: center;
  background-color: #fff;
  .logo{
    width: px2rem(150);
    height: px2rem(60);
    margin: 0 auto;
  }
  .tool-bar{
    position: absolute;
    right: 0;
    top: 50%;
    padding: 0 px2rem(30);
    transform: translateY(-50%);
    i{
      margin-left: px2rem(30);
      font-size: px2rem(40);
      color: #6c6f71;
    }
  }
}
</style>



